
.veui-field-invalid .veui-input,.veui-field-invalid .veui-input:focus,.veui-field-invalid .veui-input:hover{border-color:#ff5b5b
}
.veui-input{height:36px;line-height:34px;width:210px;padding:0 10px;vertical-align:middle;border:1px solid #dbdbdb;background-color:#fff;color:#666;transition:border-color .2s,color .2s
}
.veui-input:hover{border-color:#999
}
.veui-input:focus{outline:none;border-color:#999;color:#333
}
.veui-input:-moz-read-only,.veui-input:disabled{background-color:#f7f7f7;border-color:#dbdbdb;color:#999
}
.veui-input:disabled,.veui-input:read-only{background-color:#f7f7f7;border-color:#dbdbdb;color:#999
}
.veui-input:disabled{cursor:not-allowed
}
.veui-input:-ms-input-placeholder{color:#999
}
.veui-input::placeholder{color:#999
}
.veui-input[ui~=large]{height:42px;line-height:40px;font-size:16px
}
.veui-input[ui~=small]{height:32px;line-height:30px;font-size:12px
}
.veui-input[ui~=tiny]{height:28px;line-height:26px;font-size:12px
}
.veui-input[ui~=micro]{height:22px;line-height:20px;font-size:12px
}


