
.veui-field-invalid .veui-textarea,.veui-field-invalid .veui-textarea:focus,.veui-field-invalid .veui-textarea:hover{border-color:#ff5b5b
}
.veui-textarea{display:inline-block;position:relative;overflow:hidden;width:300px;border:1px solid #dbdbdb;padding:0;background-color:#eee;font-size:14px;line-height:36px;transition:border-color .2s,color .2s;color:#666
}
.veui-textarea-focused,.veui-textarea:hover{border-color:#999
}
.veui-textarea-focused{color:#333
}
.veui-textarea-input,.veui-textarea-measurer{font:inherit
}
.veui-textarea-input{position:relative;float:right;width:100%;height:150px;border:none;outline:none;padding:0 10px;background-color:#fff;z-index:1;resize:none
}
.veui-textarea-rows .veui-textarea-input{height:auto
}
.veui-textarea-measurer+.veui-textarea-input{max-width:calc(100% - 36px)
}
.veui-textarea-measurer{position:absolute;top:0;right:0;left:0;overflow:auto;white-space:pre-wrap;word-wrap:break-word;color:#999
}
.veui-textarea-measurer-line{overflow:hidden
}
.veui-textarea-measurer-line-number{float:left;min-width:36px;text-align:center
}
.veui-textarea-measurer-line-content{padding-right:10px;padding-left:10px;overflow:hidden;visibility:hidden
}
.veui-textarea[ui~=small]{font-size:12px;line-height:32px
}
.veui-textarea[ui~=large]{font-size:16px;line-height:42px
}
.veui-textarea.veui-disabled,.veui-textarea.veui-disabled:hover,.veui-textarea.veui-readonly,.veui-textarea.veui-readonly:hover{border-color:#dbdbdb;color:#999
}
.veui-textarea.veui-disabled .veui-textarea-input,.veui-textarea.veui-readonly .veui-textarea-input{background-color:#f7f7f7
}
.veui-textarea.veui-disabled,.veui-textarea.veui-disabled .veui-textarea-input{cursor:not-allowed
}


