
.veui-slider{width:100%;min-width:210px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:visible
}
.veui-slider,.veui-slider-track-default-wrapper{position:relative
}
.veui-slider-track-default-bg,.veui-slider-track-default-fg,.veui-slider-track-default-sp{height:4px;border-radius:2px
}
.veui-slider-track-default-bg{background-color:#dbdbdb
}
.veui-slider-track-default-fg,.veui-slider-track-default-sp{position:absolute;top:0
}
.veui-slider-track-default-sp{background-color:#c4e0fe
}
.veui-slider-track-default-fg{background-color:#3998fc
}
.veui-slider-track-default-marks{width:100%;position:absolute;top:-3px
}
.veui-slider-track-default-marks div{height:10px;border-left:1px solid #dbdbdb;position:absolute
}
.veui-slider-thumb{position:absolute;left:0;-ms-transform:translateX(-50%);transform:translateX(-50%)
}
.veui-slider-thumb:focus{outline:none
}
.veui-slider-thumb.focus-visible .veui-slider-thumb-default,.veui-slider-thumb:focus .veui-slider-thumb-default{border-color:#2e7aca
}
.veui-slider-thumb-default{width:14px;height:14px;border:2px solid #3998fc;border-radius:100%;background-color:#fff
}
.veui-slider .veui-slider-track-default{height:36px;padding-top:16px
}
.veui-slider .veui-slider-thumb{top:11px
}
.veui-slider[ui~=large] .veui-slider-track-default{height:42px;padding-top:19px
}
.veui-slider[ui~=large] .veui-slider-thumb{top:14px
}
.veui-slider[ui~=small] .veui-slider-track-default{height:32px;padding-top:14px
}
.veui-slider[ui~=small] .veui-slider-thumb{top:9px
}
.veui-slider[ui~=tiny] .veui-slider-track-default{height:28px;padding-top:12px
}
.veui-slider[ui~=tiny] .veui-slider-thumb{top:7px
}
.veui-slider[ui~=micro] .veui-slider-track-default{height:22px;padding-top:9px
}
.veui-slider[ui~=micro] .veui-slider-thumb{top:4px
}
.veui-slider.veui-disabled .veui-slider-track-default-bg,.veui-slider.veui-readonly .veui-slider-track-default-bg{background-color:#e7e7e7
}
.veui-slider.veui-disabled .veui-slider-track-default-fg,.veui-slider.veui-readonly .veui-slider-track-default-fg{background-color:#b8b8b8
}
.veui-slider.veui-disabled .veui-slider-track-default-marks div,.veui-slider.veui-readonly .veui-slider-track-default-marks div{border-color:#e7e7e7
}
.veui-slider.veui-disabled .veui-slider-thumb-default,.veui-slider.veui-readonly .veui-slider-thumb-default{border-color:#b8b8b8
}


