
.veui-tabs{position:relative
}
.veui-tabs-menu button{transition-property:border-color,background-color,color,opacity;transition-duration:.2s;background:none;border:none;padding:0
}
.veui-tabs-menu{overflow:hidden;white-space:nowrap
}
.veui-tabs-menu:after{content:"";position:absolute;left:0;top:calc(2.5em - 1px);width:100%;border-bottom:1px solid #e7e7e7
}
.veui-tabs-list{position:relative;display:inline-block;padding:0;margin:0
}
.veui-tabs-item{display:inline-block;margin-left:50px
}
.veui-tabs-item:first-child{margin-left:-10px
}
.veui-tabs-item:last-child{margin-right:-10px
}
.veui-tabs-item>.veui-tabs-item-remove{visibility:hidden;color:#999;cursor:pointer;outline:none
}
.veui-tabs-item>.veui-tabs-item-remove .veui-icon{display:block
}
.veui-tabs-item>.veui-tabs-item-remove:after{content:none
}
.veui-tabs-item>.veui-tabs-item-remove.focus-visible,.veui-tabs-item>.veui-tabs-item-remove:hover{color:#666
}
.veui-tabs-item>.veui-tabs-item-remove:active{color:#333
}
.veui-tabs-item-label.focus-visible>.veui-tabs-item-remove,.veui-tabs-item-remove:focus,.veui-tabs-item:hover>.veui-tabs-item-remove{visibility:visible
}
.veui-tabs-item>.veui-tabs-item-label{cursor:pointer;padding:0 10px;line-height:2.5;display:inline-block;text-decoration:none;outline:none;position:relative
}
.veui-tabs-item:not(.veui-tabs-item-disabled)>.veui-tabs-item-label{color:#333
}
.veui-tabs-item:not(.veui-tabs-item-disabled)>.veui-tabs-item-label:after{content:"";position:absolute;right:10px;bottom:0;left:10px;height:2px;z-index:1;transition:background-color .2s
}
.veui-tabs-item:not(.veui-tabs-item-disabled)>.veui-tabs-item-label.focus-visible:after,.veui-tabs-item:not(.veui-tabs-item-disabled)>.veui-tabs-item-label:hover:after{background-color:#999
}
.veui-tabs-item-active>.veui-tabs-item-label:after,.veui-tabs-item:not(.veui-tabs-item-disabled)>.veui-tabs-item-label:active:after{background-color:#333
}
.veui-tabs-item:first-child-label{margin-left:-10px
}
.veui-tabs-item-disabled>.veui-tabs-item-label{cursor:not-allowed;color:#999
}
.veui-tabs-extra{display:inline-block;padding-left:60px;background-color:#fff
}
.veui-tabs-list-empty+.veui-tabs-extra{padding-left:0
}
.veui-tabs-extra.veui-tabs-extra-overflow{height:2.5em;position:absolute;right:0;top:0;border-bottom:1px solid #e7e7e7;z-index:2
}
.veui-tabs-operator{display:inline-block;line-height:2.5;outline:none;cursor:pointer
}
.veui-tabs-operator .veui-icon{margin-right:6px
}
.veui-tabs-item-remove,.veui-tabs-operator .veui-icon{vertical-align:top;margin-top:calc(36px / 2 - .5em - 1px);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.veui-tabs-scroller{display:inline-block;line-height:2.5;margin-left:30px
}
.veui-tabs-scroller-left,.veui-tabs-scroller-right{display:inline-block;margin-left:5px;width:28px;height:28px;background-color:#f7f7f7;text-align:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none
}
.veui-tabs-scroller-left:first-child,.veui-tabs-scroller-right:first-child{margin-left:-10px
}
.veui-tabs-scroller-left:last-child,.veui-tabs-scroller-right:last-child{margin-right:-10px
}
.veui-tabs-scroller-left.focus-visible,.veui-tabs-scroller-left:hover,.veui-tabs-scroller-right.focus-visible,.veui-tabs-scroller-right:hover{background-color:#dbdbdb
}
.veui-tabs-scroller-left:active,.veui-tabs-scroller-right:active{background-color:#999
}
.veui-tabs[ui~=large] .veui-tabs-extra,.veui-tabs[ui~=large] .veui-tabs-item,.veui-tabs[ui~=large] .veui-tabs-menu:after,.veui-tabs[ui~=large] .veui-tabs-operator{font-size:16px
}
.veui-tabs[ui~=large] .veui-tabs-item{margin-left:50px
}
.veui-tabs[ui~=large] .veui-tabs-item:first-child{margin-left:-10px
}
.veui-tabs[ui~=large] .veui-tabs-item:last-child{margin-right:-10px
}
.veui-tabs[ui~=large] .veui-tabs-operator{padding:0 10px
}
.veui-tabs[ui~=large] .veui-tabs-item-remove,.veui-tabs[ui~=large] .veui-tabs-operator .veui-icon{margin-top:calc(42px / 2 - .5em - 1px)
}
.veui-tabs[ui~=large] .veui-tabs-scroller-left,.veui-tabs[ui~=large] .veui-tabs-scroller-right{width:32px;height:32px
}
.veui-tabs[ui~=large] .veui-tabs-scroller-left .veui-icon,.veui-tabs[ui~=large] .veui-tabs-scroller-right .veui-icon{font-size:14px;vertical-align:text-top
}
.veui-tabs[ui~=small] .veui-tabs-item{margin-left:30px
}
.veui-tabs[ui~=small] .veui-tabs-item:first-child{margin-left:-10px
}
.veui-tabs[ui~=small] .veui-tabs-item:last-child{margin-right:-10px
}
.veui-tabs[ui~=small] .veui-tabs-menu,.veui-tabs[ui~=tiny] .veui-tabs-menu{border-bottom:none
}
.veui-tabs[ui~=tiny] .veui-tabs-menu:after{display:none
}
.veui-tabs[ui~=tiny] .veui-tabs-item{margin-left:21px;position:relative
}
.veui-tabs[ui~=tiny] .veui-tabs-item:first-child{margin-left:-10px
}
.veui-tabs[ui~=tiny] .veui-tabs-item:last-child{margin-right:-10px
}
.veui-tabs[ui~=tiny] .veui-tabs-item-label:after{height:1px
}
.veui-tabs[ui~=tiny] .veui-tabs-item:before{content:"";display:inline-block;width:1px;height:14px;background-color:#e7e7e7;position:absolute;top:10px;left:-11px
}
.veui-tabs[ui~=tiny] .veui-tabs-item:first-child:before{display:none
}


